
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import Divider from "vux/src/components/divider/index";
import XHr from "vux/src/components/x-hr/index";

export default {
components: {
XHr,
Divider,
PopupPicker,
XButton,
XNumber,
XTextarea,
XAddress,
ChinaAddressV4Data,
Group,
FlexboxItem,
Flexbox,
XInput,
XHeader
},
name: "mainVux",
data() {
return {
msg: 'Hello',
addressData: ChinaAddressV4Data,
receiverName: '',
receiverPhone: '',
receiverAddr: [],
receiverAddrDetail: '',
shipperName: '',
shipperPhone: '',
shipperAddr: [],
shipperAddrDetail: '',
transTypeValue: [["普运", "次日达", "隔日", "空运"]],
transType: [],
goodsName: '',
pkgCount: 0,
remark: '',
}
},
methods: {
checkInput: function () {
if (trim(this.receiverName).length == 0) {
$vux.toast.show({
type: 'cancle',
text: '收货人不能为空',
position: 'middle'
});
return;
}
if (trim(this.receiverPhone).length == 0) {
$vux.toast.show({
type: 'cancle',
text: '收货人电话不能为空',
position: 'middle'
});
return;
}
},
submit: function () {

}
}
}



