

import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import router from './router'

Vue.use(VueRouter)
Vue.use(AjaxPlugin);
Vue.use(LoadingPlugin);
Vue.use(ToastPlugin);


FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')

Vue.prototype.$http.defaults.baseURL = 'http://192.168.8.73:8085/';
Vue.config.debug = true;
Vue.config.productionTip = false;

Vue.prototype.$http.interceptors.request.use(config => {
Vue.$vux.loading.show({
text: '正在加载...'
});
return config;
}, error => {

Vue.$vux.loading.hide();
Vue.$vux.toast.text(error.message);

return error;
})


Vue.prototype.$http.interceptors.response.use(data => {
Vue.$vux.loading.hide()
return data;
}, error => {
Vue.$vux.loading.hide();
Vue.$vux.toast.text(error.message);
return error;
})



