
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


let resdata = {
code: '',
msg: '',
data: {
},
};

export default {
components: {
Group,
Cell,
Flexbox,
FlexboxItem,
XInput,
XAddress,
ChinaAddressV4Data,
XTextarea,
Divider,
XButton,
XNumber,
Selector,
PopupPicker,
XHeader
},
data() {
return {
addressData: ChinaAddressV4Data,
waybillTypeList:[["普运", "次日达", "隔日", "空运"]],
formdata:{
sendName: '',
sendPhone: '',
sendAddress: [],
sendAddressDetail: '',
receiveName: '',
receivePhone: '',
receiveAddress: [],
receiveAddressDetail: '',
goodsName: '',
pkgCount: 0,
remark: '',
waybillType:[],
},
}
},
methods: {
submit: function () {
this.$http.post('user/createWaybill', this.formdata)
.then((res) => {
console.log(res.data);
resdata = res.data;
if (resdata.code === 1) {
console.log("成功");
this.$vux.toast.text("成功");
} else {
this.$vux.toast.show({
text: resdata.msg,
type: 'cancel',

})
}
})
.catch(function (error) {
console.log(error);

});
},
}
}



