import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/WaybillCreate'
import Main from '@/components/MainVux'

Vue.use(Router)

const router =  new Router({
  base:'/dist/',
  routes: [
    // {
    //   path: '/main',
    //   name: 'main',
    //   component: Main
    // },
  {
    path: '/',
    component: Home
  }],
})

export default router;



